import api from '../../utils/api.js';
import util from '../../utils/util.js';
const app = getApp();
export default {
	data() {
		return {
			id: '',
			userInfo: {},
			school: '',
			grade: '',
			allNum: 0,
			list: [],
			page: 0,
			isNoMore: false,
			isLoading: false
		};
	},
	onLoad() {
		uni.hideTabBar();
		this.refreshData();
	},
	onShow() {
		if(app.globalData.isRefresh){
			this.refreshData();
			app.globalData.isRefresh = false;
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.refreshData();
	},
	// 上拉触底
	onReachBottom() {
		this.getListData();
	},
	methods: {
		// 刷新数据
		refreshData() {
			const that = this;
			api.userInfo({
				success: function(res) {
					that.userInfo = res;
					that.getPhotoId(res.identity);
				}
			});
			api.index({
				success: function(res) {
					that.school = res.school;
					that.grade = res.grade;
				}
			});
			this.page = 0;
			this.isNoMore = false;
			this.list = [];
			this.getListData();
		},
		getPhotoId(identity) {
			const that = this;
			let type = 0;
			if (identity == 1) {
				type = 4;
			} else if (identity == 2) {
				type = 3;
			}
			api.checkUpload({
				data: {
					type: type
				},
				success: function(res) {
					if (res) {
						that.id = res;
					}
				}
			});
		},
		getListData() {
			const that = this;
			if (this.isNoMore || this.isLoading) {
				return false;
			}
			let page = this.page + 1;
			let size = 30;
			api.photos({
				data: {
					type: 1,
					page: page,
					size: size
				},
				success: function(res) {
					uni.stopPullDownRefresh();
					that.allNum = res.count;
					let list = res.list;
					if (list.length < size) {
						that.isNoMore = true;
					}
					if (page == 1) {
						that.list = [];
					}
					that.list = that.list.concat(list);
					that.page = page;
				},
				fail: function(res) {
					that.isNoMore = true;
				},
				complete: function(res) {
					that.isLoading = false;
				}
			});
		},
		changeDetail() {
			if (this.userInfo.identity == 1) {
				uni.navigateTo({
					url: '../studentAdd/studentAdd?id=' + this.id
				});
			} else if (this.userInfo.identity == 2) {
				uni.navigateTo({
					url: '../teacherAdd/teacherAdd?id=' + this.id
				});
			}
		}
	}
};
